import axios from "axios";
import { Toast } from "vant";
import { getAreaList } from "../../services/MineService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export const selectArea = {
  components: {
    NavigationBar
  },
  data() {
    return {
      searchText: "",
      cityCode: "2018012400001",
      cityName: "正在定位...",
      list: [],
      indexList: [],
      showSearch: false,
      searchResults: [],
      searchNull: true,
      padingtopNum: { "margin-top": this.$stbHeight + 44 + "px" },
      showLeftArrow: true
    };
  },
  watch: {
    searchText() {
      if (this.searchText !== "") {
        this.searchNull = false;
      } else {
        this.searchNull = true;
      }
    }
  },
  methods: {
    searchBtnAction() {
      if (!this.searchNull) {
        this.onSearch();
      } else {
        this.onCancel();
      }
    },

    onClickLeft() {
      // console.log('当前fromPage: ' + this.$fromPage);
      this.$router.back(-1);
    },
    selectCity() {
      this.$router.push("/selectCity");
    },
    showSerchView() {
      this.showSearch = true;
      setTimeout(() => {
        this.$refs.searchField.focus();
      }, 200);
    },
    cellClicked(area) {
      this.$router.push({
        path: "/selectHouse",
        query: {
          areaCode: area.AREA_CODE,
          areaName: area.AREA_NAME,
          cityCode: this.cityCode,
          cityName: this.cityName == "正在定位..." ? "" : this.cityName
        }
      });
    },
    onSearch() {
      // for()
      this.searchResults = []; //修复搜索小区结果不清空问题
      for (let item of this.allAreaList) {
        if (item.AREA_NAME.match(this.searchText)) {
          //修改ZHSQYYPT-4204问题 qiao 2019/12/9
          this.searchResults.push(item);
        }
      }
      if (this.searchResults.length == 0) {
        this.$toast("没有搜索到此小区");
      }
    },
    onCancel() {
      this.showSearch = false;
    },

    // 获取XX城市小区列表
    initAreaList() {
      let param = { CITY_CODE: this.cityCode };
      getAreaList(param)
        .then(data => {
          this.list = data.areaList;
          this.indexList = data.areaTitleList;
        })
        .catch(error => {
          // Toast(error);
        });
    },
    getCurrentLocation() {
      console.log(navigator);
      navigator.geolocation.getCurrentPosition(
        position => {
          //定位成功回调
          console.log("success");
          console.log(position);
          var coordtransform = require("coordtransform");
          var coord = coordtransform.wgs84togcj02(
            position.coords.longitude,
            position.coords.latitude
          );
          let longitude = coord[0]; //118.78307918718679;//gcj[0];//
          let latitude = coord[1]; //32.04093850800938;//gcj[1];//
          var url =
            "https://apis.map.qq.com/ws/geocoder/v1/?location=" +
            latitude +
            "," +
            longitude +
            "&key=HC3BZ-W7SHU-GUTVY-2MOH3-HZMC5-F7F3D&get_poi=0&output=json";
          console.log(url);
          axios
            .get(url)
            .then(data => {
              console.log(data);
              console.log(data.data);
              console.log(data.data.result);
              console.log(data.data.result.address_component);
              console.log(data.data.result.address_component.city);
              this.cityName = data.data.result.address_component.city;
              console.log(this.cityName);
            })
            .catch(error => {
              //定位失败回调
              console.log(error);
            });
        },
        error => {
          console.log(error);
        }
      );
    }
  },
  mounted() {
    this.showLeftArrow = this.$fromPage != "/";
    setTimeout(function() {
      document
        .querySelector(".search-box")
        .append(document.querySelector(".van-index-bar__sidebar"));
    }, 100);
    if (this.$userInfo.CITY_CODE) {
      this.cityCode = this.$userInfo.CITY_CODE;
    }
    let query = this.$route.query;
    if (Object.keys(query).length != 0) {
      if (query.city.CITY_CODE && query.city.CITY_NAME) {
        this.cityCode = query.city.CITY_CODE;
        this.cityName = query.city.CITY_NAME;
      } else {
        this.cityCode = query.city;
        this.cityName = query.cityName == "" ? "正在定位..." : query.cityName;
        if (query.cityName == "") {
          this.getCurrentLocation();
        }
      }
    } else {
      this.getCurrentLocation();
      console.log("开始计时");
      setTimeout(() => {
        console.log("====qiao====");
        if (this.cityName == "正在定位...") {
          console.log("++++qiao++++");
          this.cityName = "请检查设置";
        }
      }, 5000);
    }
    this.initAreaList();
  },
  computed: {
    allAreaList: function() {
      var allAreas = [];
      for (let section of this.list) {
        allAreas.push(...section.item);
        console.log(section);
      }
      return allAreas;
    }
  }
};